<template>
    <div class="card">
        <div class="h-100 container-fluid text-center p-2 bx-tada" v-if="loading">
            <i class="bx  bx-file text-primary" style="font-size: 100px;"></i>
            <h5 class="text-primary fw-bold">Cargando...</h5>
        </div>
        <div class="card-body" v-else>
            <div class="text-center" :class="json && json.fondo_icono">
                <i :class="json.icono" style="font-size:100px;"></i>            
                <h2 class="card-title">
                    {{ json.titulo }}
                </h2>
            </div>
            <form action="">
                <div class="row mx-auto text-primary" style="font-size: medium;"  v-auto-animate>                    
                    <FormSelectOpcionVue
                        :id="Math.random()*1000000+'-tipo-reporte'" :nombre="'Tipo Reporte'"
                        :opciones="json.tipos"
                        :clases="['col-md-12 m-2']" 
                        :loading="loadingResponse" 
                        :showVer="false"
                        v-model:opcion="json.tipo"
                        @change = "json.tipo != 'Mensual' && (json.mes = null)"
                    ></FormSelectOpcionVue>
                    <FormSelectOpcionVue v-if="json.tipo === 'Mensual'"
                        :id="Math.random()*1000000+'-mes-reporte'" :nombre="'Mes'"
                        :opciones="meses"
                        :clases="['col-md-12 m-2']" 
                        :loading="loadingResponse" 
                        :showVer="false"
                        v-model:opcion="json.mes"
                    ></FormSelectOpcionVue>
                    <FormSelectOpcionVue
                        :id="Math.random()*1000000+'-anio-reporte'" :nombre="'Año'"
                        :opciones="json.anios"
                        :clases="['col-md-12 m-2']" 
                        :loading="loadingResponse" 
                        :showVer="false"
                        v-model:opcion="json.anio"
                    ></FormSelectOpcionVue>
                    <button class="btn btn-primary btn-block m-2" type="button" @click="descargar" :disabled="loadingResponse">
                        <i class="bx bx-sm mr-3" :class="loadingResponse ? 'bx-spin bx-loader':'bx-download'"></i> Generar Descarga
                    </button>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
import otros_servicio   from '@/services/otros'
import servicio         from '@/services/crud'

import FormSelectOpcionVue from './FormSelectOpcion.vue'
import store from '@/store'

import { defineComponent, ref, onMounted } from 'vue'
export default defineComponent({
    /**
     * ESTRUCTURA
     */
    //reporte/{reporte}/{tipo}/{mes}/{anio}
    props: [
        'json',
    ],
    setup(props) {
        const loading = ref(true)
        const loadingResponse = ref(false)
        const anios =  ref([]);

        loading.value = false;

        const json = ref({
            icono : 'bx bxs-box bx-burst-hover mx-2',
            titulo : 'Reporte Casos',
            tipos : ['Mensual', 'Primer Trimestre', 'Segundo Trimestre', 'Tercer Trimestre', 'Anual'],
            tipo : null,
            modulo : 'CASOS',
            mes : null,
            aniosURL : 'saiv/casos/periodos',
            anios : [],
            anio : null
        });

        const meses = ref(['Enero','Febrero','Marzo','Abril','Mayo','Junio','Julio'
            ,'Agosto','Septiembre','Octubre','Noviembre','Diciembre']);

        onMounted(async() => {
            try {
                const response = await servicio.obtener(json.value.aniosURL);
                json.value.anios = response ?? [];
            } catch (error) {
                console.log(error)
            }
        })

        const descargar = async() => {
            loadingResponse.value = true;
            try { 
                let _json = json.value;

                var titulo = 'Reporte_SAIV'+ store.state.USUARIO.oficina+'_'+ _json.tipo;

                _json.tipo === 'Mensual' && (titulo += _json.mes.toUpperCase());

                titulo += '_'+ _json.anio;
                await otros_servicio.descargarArchivoExcel(
                    'saiv/descargar/excel/reporte/'+_json.modulo+'/'+((_json.tipo??'TIPO').toUpperCase())+'/'+(_json.mes??'MES')+'/'+(_json.anio??'AÑO').toUpperCase(),
                    {titulo:titulo}
                );

                
            } catch (error) {
                console.log(error);
            }finally{
                loadingResponse.value = false;
            }
        }
        const isNullOrUndef = string => { return typeof string === 'undefined' || string === null || string === 'Seleccione' };
        const mes = mes => { if(!mes) return 'x'; return meses.value.indexOf(mes)+1; }

        return {
            json,
            meses,
            anios,
            loading,
            loadingResponse,
            descargar,
        }
    },
    components:{
        FormSelectOpcionVue
    }
})
</script>